	module.exports = {"data":{"postsJson":{"title":"배신, 체포와 부인","questionsTitle":"경건한 본보기르 따르기","display":"block","bookName":"누가복음","ref":"22:1-22:71","html":"<p>   <strong>22</strong><u>1</u>유월절이라 하는 무교절이 다가오매 <u>2</u>대제사장들과 서기관들이 예수를 무슨 방도로 죽일까 궁리하니 이는 그들이 백성을 두려워함이더라<br />   <u>3</u>열둘 중의 하나인 가룟인이라 부르는 유다에게 사탄이 들어가니 <u>4</u>이에 유다가 대제사장들과 성전 경비대장들에게 가서 예수를 넘겨 줄 방도를 의논하매 <u>5</u>그들이 기뻐하여 돈을 주기로 언약하는지라 <u>6</u>유다가 허락하고 예수를 무리가 없을 때에 넘겨 줄 기회를 찾더라<br />   <u>7</u>유월절 양을 잡을 무교절날이 이른지라 <u>8</u>예수께서 베드로와 요한을 보내시며 이르시되</p><h1>예수님</h1><h2>가서 우리를 위하여 유월절을 준비하여 우리로 먹게 하라</h2><p><u>9</u>여짜오되</p><h1>베드로와 요한</h1><h3>어디서준비하기를원하시나이까</h3><p><u>10</u>이르시되</p><h1>예수님</h1><h2>보라 너희가 성내로 들어가면 물 한 동이를 가지고 가는 사람을 만나리니 그가 들어가는 집으로 따라 들어가서 <u>11</u>그 집 주인에게 이르되 선생님이 네게 하는 말씀이 내가 내 제자들과 함께 유월절을 먹을 객실이 어디 있느냐 하시더라 하라 <u>12</u>그리하면 그가 자리를 마련한 큰 다락방을 보이리니 거기서 준비하라</h2><p>하시니 <u>13</u>그들이 나가 그 하신 말씀대로 만나 유월절을 준비하니라<br />   <u>14</u>때가 이르매 예수께서 사도들과 함께 앉으사 <u>15</u>이르시되</p><h1>예수님</h1><h2>내가 고난을 받기 전에 너희와 함께 이 유월절 먹기를 원하고 원하였노라 <u>16</u>내가 너희에게 이르노니 이 유월절이 하나님의 나라에서 이루기까지 다시 먹지 아니하리라</h2><p>하시고 <u>17</u>이에 잔을 받으사 감사 기도 하시고 이르시되</p><h1>예수님</h1><h2>이것을 갖다가 너희끼리 나누라 <u>18</u>내가 너희에게 이르노니 내가 이제부터 하나님의 나라가 임할 때까지 포도나무에서 난 것을 다시 마시지 아니하리라</h2><p>하시고 <u>19</u>또 떡을 가져 감사 기도 하시고 떼어 그들에게 주시며 이르시되</p><h1>예수님</h1><h2>이것은 너희를 위하여 주는 내 몸이라 너희가 이를 행하여 나를 기념하라</h2><p>하시고 <u>20</u>저녁 먹은 후에 잔도 그와 같이 하여 이르시되</p><h1>예수님</h1><h2>이 잔은 내 피로 세우는 새 언약이니 곧 너희를 위하여 붓는 것이라<br />   <u>21</u>그러나 보라 나를 파는 자의 손이 나와 함께 상 위에 있도다 <u>22</u>인자는 이미 작정된 대로 가거니와 그를 파는 그 사람에게는 화가 있으리로다</h2><p>하시니 <u>23</u>그들이 서로 묻되 우리 중에서 이 일을 행할 자가 누구일까 하더라<br />   <u>24</u>또 그들 사이에 그 중 누가 크냐 하는 다툼이 난지라<u>25</u>예수께서 이르시되</p><h1>예수님</h1><h2>이방인의 임금들은 그들을 주관하며 그 집권자들은 은인이라 칭함을 받으나 <u>26</u>너희는 그렇지 않을지니 너희 중에 큰 자는 젊은 자와 같고 다스리는 자는 섬기는 자와 같을지니라 <u>27</u>앉아서 먹는 자가 크냐 섬기는 자가 크냐 앉아서 먹는 자가 아니냐 그러나 나는 섬기는 자로 너희 중에 있노라<br />   <u>28</u>너희는 나의 모든 시험 중에 항상 나와 함께 한 자들인즉 <u>29</u>내 아버지께서 나라를 내게 맡기신 것 같이 나도 너희에게 맡겨 <u>30</u>너희로 내 나라에 있어 내 상에서 먹고 마시며 또는 보좌에 앉아 이스라엘 열두 지파를 다스리게 하려 하노라<br />   <u>31</u>시몬아, 시몬아, 보라 사탄이 너희를 밀 까부르듯 하려고 요구하였으나 <u>32</u>그러나 내가 너를 위하여 네 믿음이 떨어지지 않기를 기도하였노니 너는 돌이킨 후에 네 형제를 굳게 하라</h2><p><u>33</u>그가 말하되</p><h1>시몬 베드로</h1><h3>주여내가주와함께옥에도, 죽는데에도가기를각오하였나이다</h3><p><u>34</u>이르시되</p><h1>예수님</h1><h2>베드로야 내가 네게 말하노니 오늘 닭 울기 전에 네가 세 번 나를 모른다고 부인하리라</h2><p>하시니라<br />   <u>35</u>그들에게 이르시되</p><h1>예수님</h1><h2>내가 너희를 전대와 배낭과 신발도 없이 보내었을 때에 부족한 것이 있더냐</h2><p>이르되</p><h1>제자들</h1><h3>없었나이다</h3><p><u>36</u>이르시되</p><h1>예수님</h1><h2>이제는 전대 있는 자는 가질 것이요 배낭도 그리하고 검 없는 자는 겉옷을 팔아 살지어다 <u>37</u>내가 너희에게 말하노니 기록된 바 그는 불법자의 동류로 여김을 받았다 한 말이 내게 이루어져야 하리니 내게 관한 일이 이루어져 감이니라</h2><p><u>38</u>그들이 여짜오되</p><h1>제자들</h1><h3>주여보소서여기검둘이있나이다</h3><p>대답하시되</p><h1>예수님</h1><h2>족하다</h2><p>하시니라<br />   <u>39</u>예수께서 나가사 습관을 따라 감람 산에 가시매 제자들도 따라갔더니 <u>40</u>그 곳에 이르러 그들에게 이르시되</p><h1>예수님</h1><h2>유혹에 빠지지 않게 기도하라</h2><p>하시고 <u>41</u>그들을 떠나 돌 던질 만큼 가서 무릎을 꿇고 기도하여 <u>42</u>이르시되</p><h1>예수님</h1><h2>아버지여 만일 아버지의 뜻이거든 이 잔을 내게서 옮기시옵소서 그러나 내 원대로 마시옵고 아버지의 원대로 되기를 원하나이다</h2><p>하시니 <u>43</u>천사가 하늘로부터 예수께 나타나 힘을 더하더라 <u>44</u>예수께서 힘쓰고 애써 더욱 간절히 기도하시니 땀이 땅에 떨어지는 핏방울 같이 되더라<br />   <u>45</u>기도 후에 일어나 제자들에게 가서 슬픔으로 인하여 잠든 것을 보시고<u>46</u>이르시되</p><h1>예수님</h1><h2>어찌하여 자느냐 시험에 들지 않게 일어나 기도하라</h2><p>하시니라<br />   <u>47</u>말씀하실 때에 한 무리가 오는데 열둘 중의 하나인 유다라 하는 자가 그들을 앞장서 와서 <u>48</u>예수께 입을 맞추려고 가까이 하는지라 예수께서 이르시되</p><h1>예수님</h1><h2>유다야 네가 입맞춤으로 인자를 파느냐</h2><p>하시니 <u>49</u>그의 주위 사람들이 그 된 일을 보고 여짜오되</p><h1>제자들</h1><h3>주여우리가칼로치리이까</h3><p>하고 <u>50</u>그 중의 한 사람이 대제사장의 종을 쳐 그 오른쪽 귀를 떨어뜨린지라<br />   <u>51</u>예수께서 일러 이르시되</p><h1>예수님</h1><h2>이것까지 참으라</h2><p>하시고 그 귀를 만져 낫게 하시더라<br />   <u>52</u>예수께서 그 잡으러 온 대제사장들과 성전의 경비대장들과 장로들에게 이르시되</p><h1>예수님</h1><h2>너희가 강도를 잡는 것 같이 검과 몽치를 가지고 나왔느냐 <u>53</u>내가 날마다 너희와 함께 성전에 있을 때에 내게 손을 대지 아니하였도다 그러나 이제는 너희 때요 어둠의 권세로다</h2><p>하시더라<br />   <u>54</u>예수를 잡아 끌고 대제사장의 집으로 들어갈새 베드로가 멀찍이 따라가니라 <u>55</u>사람들이 뜰가운데 불을 피우고 함께 앉았는지라 베드로도 그 가운데 앉았더니 <u>56</u>한 여종이 베드로의 불빛을 향하여 앉은 것을 보고 주목하여 이르되</p><h1>여종</h1><h4>이 사람도 그와 함께 있었느니라</h4><p>하니 <u>57</u>베드로가 부인하여 이르되</p><h1>시몬 베드로</h1><h3>이여자여내가그를알지못하노라</h3><p>하더라<br />   <u>58</u>조금 후에 다른 사람이 보고 이르되</p><h1>재판에 있던 사람들</h1><h4>너도 그 도당이라</h4><p>하거늘 베드로가 이르되</p><h1>시몬 베드로</h1><h3>이 사람아  나는 아니로라</h3><p>하더라<br />   <u>59</u>한 시간쯤 있다가 또 한 사람이 장담하여 이르되</p><h1>재판에 있던 사람들</h1><h4>이는 갈릴리 사람이니 참으로 그와 함께 있었느니라</h4><p><u>60</u>베드로가 이르되</p><h1>시몬 베드로</h1><h3>이 사람아 나는 네가 하는 말을 알지 못하노라</h3><p>고 아직 말하고 있을 때에 닭이 곧 울더라<br />   <u>61</u>주께서 돌이켜 베드로를 보시니 베드로가 주의 말씀 곧 오늘 닭 울기 전에 네가 세 번 나를 부인하리라 하심이 생각나서 <u>62</u>밖에 나가서 심히 통곡하니라<br />   <u>63</u>지키는 사람들이 예수를 희롱하고 때리며 <u>64</u>그의 눈을 가리고 물어 이르되</p><h1>성전 지기들</h1><h4>선지자 노릇 하라 너를 친 자가 누구냐</h4><p>하고 <u>65</u>이 외에도 많은 말로 욕하더라<br />   <u>66</u>날이 새매 백성의 장로들 곧 대제사장들과 서기관들이 모여서 예수를 그 공회로 끌어들여 <u>67</u>이르되</p><h1>공회원들</h1><h4>네가 그리스도이거든 우리에게 말하라</h4><p>대답하시되</p><h1>예수님</h1><h2>내가 말할지라도 너희가 믿지 아니할 것이요 <u>68</u>내가 물어도 너희가 대답하지 아니할 것이니라 <u>69</u>그러나 이제부터는 인자가 하나님의 권능의 우편에 앉아 있으리라</h2><p>하시니 <u>70</u>다 이르되</p><h1>공회원들</h1><h4>그러면 네가 하나님의 아들이냐</h4><p>대답하시되</p><h1>예수님</h1><h2>너희들이 내가 그라고 말하고 있느니라</h2><p><u>71</u>그들이 이르되</p><h1>공회원들</h1><h4>어찌 더 증거를 요구하리요 우리가 친히 그 입에서 들었노라</h4><p>하더라</p>","path":"/337","Question1":"이 말씀에서 당신이 따라야 하는 행동이 있습니까? 있다면 무엇입니까?","Question2":"이번 주에 당신은 어떤 결정을 내릴 수 있습니까?","Question3":"이 말씀에서 당신이 회개해야 하는 행동이 있습니까?","Question4":"이번 주에 당신은 어떠한 결정을 내릴 수 있습니까?","prevPath":"/336","nextPath":"/338"}},"pathContext":{}}

